import React from 'react';
import styles from './index.less';

const DropdownContent = ({ data }: any) => {
  let batteryTimeDesc = '';
  if (data.charging) {
    if (Number.isFinite(data.chargingTime)) {
      if (data.chargingTime === 0) {
        batteryTimeDesc = '';
      } else {
        batteryTimeDesc = ` ${data.chargingTime / 60} `;
      }
    }
  } else {
    if (Number.isFinite(data.dischargingTime)) {
      batteryTimeDesc = ` ${data.dischargingTime / 60} `;
    }
  }
  return (
    <div className={styles.dropdownContent}>
      <div className={styles.title}>
        <h1></h1>
        <span>{`${(data.level * 100).toFixed()}%`}</span>
      </div>
      <div className={styles.desc}>
        <div>{`${data.charging ? '' : ''}`}</div>
        <div>{batteryTimeDesc}</div>
      </div>
    </div>
  );
};

export default DropdownContent;
